import React from 'react';


class Bugs extends React.Component {
     

    render() {
        return (
            <div className="container mt-5">
                <center>
                    <h3>Happy to see you here</h3>
                    <p>1. If you face any bug in commands you can submit that.</p>
                    <p>2. If you need new feture you can ask for that.</p>
                    <a className="btn btn-outline-info" rel="noopener noreferrer" target="_blank" href="https://github.com/hackercmd-web/hackercmd-web.github.io/issues">Submit Bug Information</a>
                </center>
            </div>
        )
    }
}

export default Bugs

