import React from 'react'
import Logo from '../logo.png'
import {Link} from 'react-router-dom'
class Header extends React.Component {

    render() {
        return (
            <nav className="site-header sticky-top py-1">
                <div className="container d-flex flex-column flex-md-row justify-content-between">
                    <Link className="py-2" to="/">
                        <img src={Logo} alt="Logo"  />
                    </Link>
                    <Link  className="py-2 d-none d-md-inline-block" to="/">Home</Link>
                    <Link  className="py-2 d-none d-md-inline-block" to="/download">Download</Link>
                    <Link  className="py-2 d-none d-md-inline-block" to="/documentation">Documentation</Link>
                    <Link  className="py-2 d-none d-md-inline-block" to="/donate">Donate</Link>
                    <Link  className="py-2 d-none d-md-inline-block" to="/about">About</Link>
                    <Link  className="py-2 d-none d-md-inline-block" to="/other-freebies">My other freebies</Link>
                    <Link  className="py-2 d-none d-md-inline-block" to="/bugs">[ Report-Bug ]</Link>
                </div>
            </nav>

        )
    }
}

export default Header