import React from 'react'

class FreebiesItem extends React.Component{

    render(){
        return(
            <tr>
                <td><img src={this.props.item.image} alt="icon" style={{maxWidth:'50px'}} decoding="async"></img></td>
                <td>{this.props.item.name}</td>
                <td>{this.props.item.platform}</td>
                <td>{this.props.item.currentversion}</td>
                <td>{this.props.item.publisheddate}</td>
                <td>{this.props.item.description}</td>
                <td><a target="_blank" rel="noopener noreferrer" href={this.props.item.link}>{this.props.item.link}</a></td>
            </tr>
        )
    }
}

export default FreebiesItem;