import React from 'react';
import axios from 'axios';
import toastr from 'toastr'
import LoadingGif from './loading.gif'
import FreebiesItem from './freebiesItem';
class myOtherFreebies extends React.Component {

    state = {
        isLoading: true,
        data: []
    }

    componentDidMount() {
        let url = "https://cors-anywhere.herokuapp.com/https://script.google.com/macros/s/AKfycbze3xAryaAbaxQWk1MkXiRhXySvpwzIcrn40c4wsf2LYeIo7Wow/exec"
        axios({
            method: 'get',
            redirect: 'manual',
            maxRedirects: 0,
            
            url: url,

        })
            .then((response) => {
                 
                this.setState({
                    isLoading: false,
                    data: response.data
                })


            })
            .catch((error) => {
                console.log(error)
                this.setState({
                    isLoading: false
                })
                toastr.error('Data Collection error!', 'It will be fixed soon')
            })
    }

    render() {
        let { isLoading } = this.state

        return (
            <div className="container-fluid" style={{ marginTop: '50px' }}>
                <div className="table-responsive">
                {
                    isLoading ?
                        <center><img style={{ maxWidth: '200px' }} src={LoadingGif} alt="loading..." /></center>
                        :
                        <center>
                            <table className="table table-hover">
                            <thead className="thead-light">
                                <tr>
                                <th scope="col">Icon</th>
                                <th scope="col">Name</th>
                                <th scope="col">Platform</th>
                                <th scope="col">Latest Version</th>
                                <th scope="col">Publisheddate</th>
                                <th scope="col">Description</th>
                                <th scope="col">Link</th>
                                </tr>
                            </thead>
                                <tbody>
                                    {
                                        this.state.data.map((res) =>
                                            <FreebiesItem key={res.id} item={res} />
                                        )
                                    }
                                </tbody>
                            </table>
                        </center>

                }
                </div>
            </div>
        )
    }
}

export default myOtherFreebies

