import React from 'react';
import Header from './components/header'
import Slogan from './components/slogan'
import Animation from './components/Animation';
import Footer from './components/footer'
import About from './components/about';
import Download from './components/Download'
import Documentation from './components/Documentation'
import Donate from './components/donate'
 

import {
  BrowserRouter as Router,
  Switch,
  Route,
  
} from "react-router-dom";
import myOtherFreebies from './components/myOtherFreebies';
import Bugs from './components/bugs';

 

class App extends React.Component {

  

  render() {

    return (
      <Router>

        <div style={{ backgroundColor: '#F5F5F5' }}>

          <Header />
          <Switch>
            <Route exact path="/">
              <Slogan />
              <Animation />
            </Route>
            <Route path="/download" component={Download}></Route>
            <Route path="/about" component={About}></Route>
            <Route path="/documentation" component={Documentation}></Route>
            <Route path="/donate" component={Donate}></Route>
            <Route path="/other-freebies" component={myOtherFreebies}></Route>
            <Route path="/bugs" component={Bugs}></Route>
          </Switch>
          <Footer />
        </div>
      </Router>
    );

  }
}

export default App;
