import React from 'react'
import SuccessImage from './tick.png'
class Success extends React.Component{

    render(){
        return(
            <div style={{marginTop: "100px"}}>
                <center>
                <div class="alert alert-success" role="alert">
                    Check your email. A download link has been send to your email
                </div>
                    <img src={SuccessImage} style={{maxWidth: '200px'}} alt="Success, Check your email" img/>
                </center>
            </div>
        )
    }

}

export default Success;