import React from 'react'
import {Link} from 'react-router-dom'

class Slogan extends React.Component{
    render(){
        return (
            <div className="position-relative overflow-hidden p-3 p-md-5 m-md-3 text-center bg-light">
            <div className="col-md-5 p-lg-5 mx-auto my-5">
                <h1 className="display-4 font-weight-normal"><b style={{color:'#1FDE31'}}>HackerCMD</b></h1>
                <p className="lead font-weight-normal">An Advance Command Prompt for you. Make your command prompt like a Linux terminal. <br/>
                200+ Terminal Commands in Command Prompt
                </p>
                <Link className="btn btn-outline-success" to="/download">Download Version 1.0.0 - It's Free !</Link>
            </div>
            <div className="box-shadow d-none d-md-block" />
            <div className="box-shadow d-none d-md-block" />
            </div>
        )
    }
}

export default Slogan