import React from 'react'
import DonateBarCode from './mybtcAddress.png'
import toastr from 'toastr'
toastr.options = {
    "positionClass": "toast-top-right",
    "newestOnTop": true,
    "progressBar": true,
    "timeOut": 10000
}


class Donate extends React.Component{

    
    copyToClipBoard(){
        navigator.clipboard.writeText('3JrR6vssJ3BP6TuKEmGha5K4NQgToV8JDu')
        toastr.success('Bitcoin address copied to your clipboard.', 'Copied!')
    }
    render(){
        return(
            <center>
                <h3 style={{marginTop:'40px'}}>Bitcoin Address</h3>
                <img style={{maxWidth: '250px', marginTop: '40px'}} src={DonateBarCode} alt="profiel"></img>
                <br/><br/>
                <i>3JrR6vssJ3BP6TuKEmGha5K4NQgToV8JDu</i><br/><br/>
                
                <button onClick={this.copyToClipBoard} className="btn btn-sm btn-outline-info">Copy Address</button>
                 
            </center>
        )
    }
}

export default Donate;